import axios from 'axios'
import qs from 'qs'
//import Vue from 'vue'

export function ajax(option) {
		// 1.创建axios的实例
		const instance = axios.create({
		//	baseURL: 'http://www.hzqsn.com:8082/youngcenter_web_app',
			timeout: 5000
		});

		// 配置请求和响应拦截
		instance.interceptors.request.use(config => {
			// console.log('来到了request拦截success中');
			// 1.当发送网络请求时, 在页面中添加一个loading组件, 作为动画

			// 2.某些请求要求用户必须登录, 判断用户是否有token, 如果没有token跳转到login页面

			// 3.对请求的参数进行序列化(看服务器是否需要序列化)
			// config.data = qs.stringify(config.data)
			// console.log(config);

			// 4.等等
/* 
			Vue.$vux.loading.show({
				text: '正在发送网络请求，请稍候...'
			}) */
			console.log(config)
			return config
		}, err => {
			// console.log('来到了request拦截failure中');
			return err
		})

		instance.interceptors.response.use(response => {
			// console.log('来到了response拦截success中');
//			Vue.$vux.loading.hide()
			return response.data
		}, err => {
//			Vue.$vux.loading.hide()
			console.log('来到了response拦截failure中');
    		console.log(err);
      		if (err && err.response) {
				switch (err.response.status) {
					case 400:
						err.message = '请求错误'
						break
					case 401:
						err.message = '未授权的访问'
						break
					case 404:
						err.message = '访问路径不存在'
						break
				}
			}
			return err
		})

		// 2.传入对象进行网络请求
		return instance(option)
}

export function HttpGet(baseURL, url, data={}, contentType='application/json'){
    return ajax({
		baseURL,
        url,
        method: 'get',
		params:data,
		headers: {
			'Content-Type': contentType
		}
    })
}

export function HttpPost(baseURL, url, data={}, contentType='application/json'){
	console.log("content-type:"  + contentType)
    return ajax({
		baseURL,
        url,
        method: 'post',
        data,
		headers: {
			'Content-Type': contentType
		}
    })
}