import {
    SET_BRANCHES
} from './mutation_types'

//import { getClass, loginOn, applyClass } from '../http/api'
import { getNearbyBranches } from '../http/query'
// import { getSignAddress } from '../http/query'

export default {
    async getBranchesList({commit, state}, payload){
        const result = await getNearbyBranches((payload && payload.position) || state.myPosition, payload && payload.sndBranch)
        if(!result){
            alert('数据获取失败')
            return
        }
        commit(SET_BRANCHES, {result})
    }
}