import { HttpGet } from './ajax'
import { bgps_gps } from '../utils/common'

// let baseURL = ''
let baseURL = ''
if(process.env.NODE_ENV === 'production')
    baseURL = '/yunworkers'
else
    baseURL = '/YunWorkers'

export function getNearbyBranches(position={}, sndBranch){//首页搜索附近网点
    console.log('in query.js ,getNearbyBranches');
    if(!position.lng && !position.lat){
        console.log('you have to send a position')
        return
    }
    let gdPoint = bgps_gps(position.lng, position.lat)
    return HttpGet(baseURL, '/queryNearbyBranches', {
        lng: gdPoint.lng,
        lat: gdPoint.lat,
        sndBranch
    })
}

export function getBranchesByKeywordsAndSndBranch(position, keywords, sndBranch){//首页搜索，只查回15条
    if(!keywords && !sndBranch){
        console.log('keywords and sndBranch must be send')
        return
    }
    let gdPoint = bgps_gps(position.lng, position.lat)
    return HttpGet(baseURL, '/queryBranchesByKeywordsAndSndBranch', {
        lng: gdPoint.lng,
        lat: gdPoint.lat,
        keywords,
        sndBranch
    })
}

export function getPrimaryBranches(sndBranch){//根据二级分行机构号查询一级支行
    if(!sndBranch){
        console.log('sndBranch is null')
        return
    }
    return HttpGet(baseURL, '/queryPrimayBranches', {
        sndBranch
    })
}

export function getBranchesBySndBranch(sndBranch, pageNo, pageSize){
    if(!sndBranch){
        console.log('sndBranch is null')
        return
    }
    return HttpGet(baseURL, '/queryBranchesBySndBranch', {
        sndBranch,
        pageNo,
        pageSize
    })
}

export function getBranchesByPrimaySubBranch(primaySubBranch, pageNo, pageSize){
    if(!primaySubBranch){
        console.log('sndBranch is null')
        return
    }
    return HttpGet(baseURL, '/queryBranchesByPrimaySubBranch', {
        primaySubBranch,
        pageNo,
        pageSize
    })
}

export function getBranchesByKeywords(keywords, pageNo, pageSize){
    if(!keywords){
        console.log('keywords is null')
        return
    }
    return HttpGet(baseURL, '/queryBranchesByKeywords', {
        keywords,
        pageNo,
        pageSize
    })
}
