import {
    SET_CITY,
    SET_POSITION,
    SET_BRANCHES
} from './mutation_types'

export default {
    [SET_CITY] (state, payload){
        state.myCity = payload
    },
    [SET_POSITION] (state, payload){
        state.myPosition = payload
    },
    [SET_BRANCHES] (state, payload){
        state.nearbyBranches = payload
    }
}